  <div class="navigation">
      <router-link class="logo" :to=" { name: 'Home' }">Quyen</router-link>
      <div class="box-search">
          <input type="text" class="search" placeholder="Tìm kiếm sản phẩm...">
          <Search class="icon search" />
      </div>
      <div class="user">
          <Buy class="icon buy" v-if="!mobile"/>
          <User class="icon user" v-if="!mobile"/>
          <Menu class="icon menu" @click="toggleMobileNav" v-show="mobile" />
      </div>
  </div>
  <div class="category" v-show="!mobile">
      <div>
          Sản Phẩm
          <div class="product" v-show="!mobile">
              <div>
                  <h3>Áo Nam</h3>
                  <ul>
                      <li>
                          <router-link class="link" to="#" >Áo Polo</router-link>
                      </li>
                      <li>
                          <router-link class="link" to="#" > Áo Phông</router-link>
                      </li>
                      <li>
                          <router-link class="link" to="#" >Áo Sơ Mi</router-link>
                      </li>
                      <li>
                          <router-link class="link" to="#" >Áo Hoodie</router-link>
                      </li>
                      <li>
                          <router-link class="link" to="#" >Áo Khoác</router-link>
                      </li>
                  </ul>
                </div>
              <div>
                  <h3>Quần Nam</h3>
                  <ul>
                      <li><router-link class="link" to="#">Quần Âu</router-link></li>
                      <li><router-link class="link" to="#">Quần Jean</router-link></li>
                      <li><router-link class="link" to="#">Quần Kaki</router-link></li>
                      <li><router-link class="link" to="#">Quần Short</router-link></li>
                  </ul>
                </div>
              <div>
                  <h3>Phụ Kiện Nam</h3>
                  <ul>
                      <li><router-link class="link" to="#">Balo Túi Đeo Chéo</router-link></li>
                      <li><router-link class="link" to="#">Kính</router-link></li>
                      <li><router-link class="link" to="#">Mũ</router-link></li>
                      <li><router-link class="link" to="#">Ví Da</router-link></li>
                  </ul>
                </div>
              <div>
                  <h3>Giày Dép Nam</h3>
                  <ul>
                      <li><router-link class="link" to="#">Giày</router-link></li>
                      <li><router-link class="link" to="#">Dép</router-link></li>
                  </ul>
                </div>
          </div>
      </div>
      <div>Khuyến Mãi</div>
      <div>Tin Tức</div>
      <div>Hệ Thống Cửa Hàng</div>
  </div>
  <transition name="mobile-nav">
    <div class="category-mobile" v-if="mobile" v-show="mobileNav">
      <div class="product">
        <div @click="toggleCate(1)">
          <h3>Áo Nam</h3>
          <ul v-show="check==1">
            <li>
                <router-link class="link" to="#" >Áo Polo</router-link>
            </li>
            <li>
                <router-link class="link" to="#" > Áo Phông</router-link>
            </li>
            <li>
                <router-link class="link" to="#" >Áo Sơ Mi</router-link>
            </li>
            <li>
                <router-link class="link" to="#" >Áo Hoodie</router-link>
            </li>
            <li>
                <router-link class="link" to="#" >Áo Khoác</router-link>
            </li>
          </ul>
        </div>
        <div @click="toggleCate(2)">
          <h3>Quần Nam</h3>
          <ul v-show="check==2">
            <li><router-link class="link" to="#">Quần Âu</router-link></li>
            <li><router-link class="link" to="#">Quần Jean</router-link></li>
            <li><router-link class="link" to="#">Quần Kaki</router-link></li>
            <li><router-link class="link" to="#">Quần Short</router-link></li>
          </ul>
        </div>
        <div @click="toggleCate(3)">
          <h3>Phụ Kiện Nam</h3>
          <ul v-show="check==3">
            <li><router-link class="link" to="#">Balo Túi Đeo Chéo</router-link></li>
            <li><router-link class="link" to="#">Kính</router-link></li>
            <li><router-link class="link" to="#">Mũ</router-link></li>
            <li><router-link class="link" to="#">Ví Da</router-link></li>
          </ul>
        </div>
        <div @click="toggleCate(4)">
          <h3>Giày Dép Nam</h3>
          <ul v-show="check == 4">
            <li><router-link class="link" to="#">Giày</router-link></li>
            <li><router-link class="link" to="#">Dép</router-link></li>
          </ul>
        </div>
      </div>
    </div>
  </transition>
</div>
</template>

<script>
import Search from '../assets/Icons/arrow-right-light.svg'
import User from '../assets/Icons/user-alt-light.svg'
import Buy from '../assets/Icons/buy.svg'
import Menu from '../assets/Icons/bars-regular.svg'
export default {
    name: 'Navigation',
    components: {
        Search,
        User,
        Buy,
        Menu
    },
    data() {
        return {
            check: 0,
            mobile: null,
            mobileNav: null,
            windownWidth: null,
        }
    },
    created() {
        window.addEventListener('resize', this.checkScreen);
        this.checkScreen();
    },
    methods: {
        checkScreen(){
            this.windownWidth = window.innerWidth;
            if(this.windownWidth <= 768) {
            this.mobile = true;
            return;
        }
        this.mobile = false;
        this.mobileNav = false;
        },
        toggleMobileNav() {
            this.mobileNav = !this.mobileNav;
        },
        toggleCate(number){
            if(this.check == number) {
                this.check = 0;
            }
            else {
                this.check = number;
            }
        }
    }
}
</script>

<style scoped>
.nav {
    background-color: #ccc; 
    padding-top: 20px;
}
.navigation {
    height: 100px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}
.logo {
    font-size: 26px;
    text-decoration: none;
    height: 46px;
    width: 250px;
    display: inline-block;
    text-align: center;
    width: 100%;
    overflow: hidden;
    color: rgb(2, 0, 141);
    font-weight: 600;
}
.box-search {
    height: 48px;
    display: flex;
    align-items: center;
    position: relative;
    flex: 1;
    border-radius: 4px;
    overflow: hidden;
    margin-left: 10px;
}
.box-search input {
    height: 100%;
    width: 100%;
    font-size: 16px;
    outline: none;
    padding: 0 52px 0 8px;
    border: transparent;
}
.box-search .icon {
    padding: 16px;
    height: 100%;
    position: absolute;
    right: 0;
}
.box-search .icon:hover {
    background-color: #ccc;
    opacity: 0.8;
}
.icon {
    cursor: pointer;
}
.icon.user {
    height: 26px;
    width: auto;
    cursor: pointer;
}
.icon.buy {
    height: 36px;
    width: auto;
}
.icon.menu {
    height: 26px;
    width: auto;
}
.user {
    margin: 0 20px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.category {
    display: flex;
    justify-content: space-between;
    margin: 0 6px;
    padding: 6px 0;
    position: relative;
}
.category div {
    text-align: center;
    cursor: pointer;
}
.category div:last-child {
    grid-column: 4 /6;
}
.category-mobile {
  position: fixed;
  top: 0;
  left: 0;
  background-color: #f1f1f1;
  box-shadow: 6px 0 4000px 20px rgba(0, 0, 0, 0.5);
  height: 100%;
}
.category-mobile .product{
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.category-mobile .product div ul{
    animation: zoombar 0.4s ease-out;
}
@keyframes zoombar {
      from {
        transform-origin: top;
        transform: scaleY(0);
      }
      to {
          height: 100%;
          transform: scaleY(1);
      }
    }
.category-mobile h3 {
  border-bottom: 1px solid #ccc;
  padding: 10px 20px;
  cursor: pointer;
}
.category-mobile li {
  list-style: none;
}
@media (min-width: 768px) {
    .navigation {
        align-items: center;
        height: 56px;
    }
    .logo,
    .user {
        width: 250px;
    }
    .box-search {
        margin-left: 0;
        height: 38px;
    }
    .box-search .icon {
        padding: 10px;
    }
    .category {
        display: flex;
        justify-content: center;
        padding: 0;
        margin: 0;
        flex-direction: row;
    }
    .category div:not(:last-child) {
        margin-right: 16px;
    }
}
.category-mobile ul li:nth-child(even) {
    background-color: #dedede;
}
.category-mobile ul li:nth-child(odd) {
    background-color: #ccc;
}
.link {
        display: inline-block;
        width: 100%;
        padding: 4px 0;
        color: #000;
        font-weight: 500;
        transition: all 0.2s ease;
        padding: 16px 6px;
}
.link:hover {
    color: rgb(255, 0, 0);
    transform: translateX(4px);
}
@media (min-width: 768px) {
    .category .product {
        position: absolute;
        display: none;
        background-color: #f1f1f1;
        width: 100vw;
        left: 0;
        justify-content: space-around;
        top: calc(100% +4px);
        animation: zoombar 0.08s ease-out;
        box-shadow: 0 6px 4px -1px rgba(0, 0, 0, 0.1), 0 6px 2px -1px rgba(0, 0, 0, 0.06);
    }
    @keyframes zoombar {
      from {
        transform: scaleY(0);
        transform-origin:  top;
      }
      to {
        transform: scaleY(1);
      }
    }
    .category .product::before {
        content: "";
        width: 100vw;
        height: 6px;
        position: absolute;
        background-color: transparent;
        top: -3px;
    }
    .category>div:first-child:hover .product{
        display: flex;
    }
    ul {
        list-style: none;
    }
    .link {
        display: inline-block;
        width: 100%;
        padding: 12px 0;
        color: #000;
    }
    li {
        text-align: left;
        padding-left: 6px;
    }
    h3 {
        border-bottom: 1px solid #888;
    }
}
.mobile-nav-enter-active,
.mobile-nav-leave-active {
  transition: all 0.4s ease-out;
}
.mobile-nav-enter {
  transform: translateX(-100vw);
}
.mobile-nav-enter-to {
  transform: translateX(0);
}
.mobile-nav-leave {
  transform: translateX(0);
}
.mobile-nav-leave-to {
  transform: translateX(-100vw);
}
</style>